import { Loader } from "@googlemaps/js-api-loader";
import "../styles/style.css";
import React from "react";
import { useEffect, useState } from "react";
// import pool from "../../server/database";
import Axios from "axios";
// import g from "./Functions";

const google = window.google;
export function Map(props) {
  const [data, setData] = useState(null);
  const [name, setName] = useState(); 
  const [catID, setCatID] = useState(); // used to color the different icons by category
  const [catName, setCatName] = useState(); // used to choose what markers to show based in the category clicked

  React.useEffect(() => {
    if (props.category) {
      console.log(props.category, "props.category");
      setCatName(props.category); //Black History, American Revolution
      let d = JSON.stringify({
        //   description,
        //   category,
        catName,
      });
    

      Axios.post("/p/f21-12/f21-v2/map/getCategoryByName", d, {
        headers: { "Content-Type": "application/json" },
      }).then((response) => {
        setData(response.data);
        console.log(response.data, "markers");
      });
    } else {
      fetch("/p/f21-12/f21-v2/map")
        .then((res) => res.json())
        .then((response) => {
          setData(response);
          // console.log(data, "data");
          // console.log(response);
        });
    }
  }, [props.category, catName]);

  useEffect(() => {
    Axios.get("/p/f21-12/f21-v2/map/getCategory")
      .then((res) => {
        setName(res.data);
      })
      .catch((err) => {
        console.log(err, "err");
      });
  }, []);

  const loader = new Loader({
    apiKey: "AIzaSyAvw8sOvIShLC2gWYbjakB1urJ3TmNbztw",
    version: "weekly",
  });
  var map;
  loader
    .load()
    .then(() => {
      map = new google.maps.Map(document.getElementById("map"), {
        center: { lat: data[0].lat, lng: data[0].lng },
        zoom: 8,
        mapTypeControl: true,
        mapTypeControlOptions: {
          style: google.maps.MapTypeControlStyle.HORIZONTAL_BAR,
          position: google.maps.ControlPosition.TOP_CENTER,
        },
      });

      // const triangleCoords = [
      //   { lat: 25.774, lng: -80.19 },
      //   { lat: 18.466, lng: -66.118 },
      //   { lat: 32.321, lng: -64.757 },
      //   { lat: 25.774, lng: -80.19 },
      // ];
      // Construct the polygon.
      // const bermudaTriangle = new google.maps.Polygon({
      //   paths: triangleCoords,
      //   strokeColor: "#FF0000",
      //   strokeOpacity: 0.8,
      //   strokeWeight: 2,
      //   fillColor: "#FF0000",
      //   fillOpacity: 0.35,
      // });

      // bermudaTriangle.setMap(map);

      // marker[i].addListener("click", ()=>{
      //   console.log(marker)
      // })
      //Outline map area -------------------------------

      //------------------------------------------------------

      for (var i = 0; i < data.length; i++) {
        var markers = data[i]; //markers variables represent the Map object coming from the database

        var marker = new google.maps.Marker({
          position: {
            lat: markers.lat,
            lng: markers.lng,
          },
          map,
          title: markers.title,
          //  icon:
        });
        addInfoBox(marker, markers);
        addIconColor(markers, marker);

        marker.addListener("click", function () {
          map.panTo(this.getPosition());
          map.setZoom(10);
          // map.setTilt(30);
        });
      }
    })
    .catch((e) => console.log(e));

  function addInfoBox(marker, d) {
    const contentString =
      "<div>" +
      "<h1>" +
      d.title +
      "</h1>" +
      "<p>" +
      d.description +
      "</p>" +
      "<a href =" +
      d.link +
      "/> " +
      d.link +
      "</a>" +
      "</div>";
    const infowindow = new google.maps.InfoWindow({
      content: contentString,
    });

    marker.addListener("click", () => {
      infowindow.open(marker.get("map"), marker);
    });
    // getCategoryViaId(d.catID);
    // marker.setIcon(addIconColor(category));
  }
  
function addIconColor(d, marker) {
  
    const found = name.find((g) => g.catID == d.catID);
    const str = "http://maps.google.com/mapfiles/ms/icons/";
    switch (found.catName) {
      case "American Revolution":
        marker.setIcon(str + "red-dot.png");
        break;
      case "Black History":
        marker.setIcon(str + "ltblue-dot.png");
        break;
      case "Civil Rights":
        marker.setIcon(str + "yellow-dot.png");
        break;
      case "Culture":
        marker.setIcon(str + "green.png");
        break;
      case "Government":
        marker.setIcon(str + "green-dot.png");
        break;
      case "Music, Art, and Theater":
        marker.setIcon(str + "ltblue-dot.png");
        break;
      case "Native Americans":
        marker.setIcon(str + "blue-dot.png");
        break;
      case "Nature":
        marker.setIcon(str + "pink.png");
        break;
      case "U.S. Presidents":
        marker.setIcon(str + "pink-dot.png");
        break;
      case "Women":
        marker.setIcon(str + "blue-dot.png");
        break;
      default:
        marker.setIcon(
          "http://maps.google.com/mapfiles/ms/icons/green-dot.png"
        );
        break;
    }
}
  // const [map, setMap] = useState(0);

  return <div id="map"></div>;
}
